@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap);
:root {
  --primary: #ee6e73;
  --primary-dark: #d3393f;
  --light: #e6e6e6;
  --dark: #111;
}

* {
  font-family: "Spartan", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #e6e6e6;
  background-color: var(--light);
}

.body {
  width: 100%;
  height: 100%;
}

#product_card .card-title {
  font-weight: 500;
}

#product_card .product-type {
  font-size: 0.9rem;
}

#product_card .actions {
  display: flex;
  justify-content: space-between;
  padding: 12px 0 0 0;
}

#product_card .btn {
  font-family: "Spartan", sans-serif;
}

#product_card .btn i.left {
  margin: 0 0 0 0;
}

#product_card .image-loading-wrapper {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  min-height: 18rem;
}

#home_content {
  height: 100%;
  width: 100%;
}

#login_content {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  min-height: 86vh;
}

#login_content .title {
  text-align: center;
  font-weight: 600;
}

#login_content form {
  margin-left: 0;
}

#login_content .row {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-left: 0;
  margin: 0;
}

#login_content form .btn {
  margin-right: 1rem;
  font-family: "Spartan", sans-serif;
}

#login_content form .signup-link {
  margin-left: 1rem;
  margin-top: 0.5rem;
}

#login_content form .bottom-part {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

#signup_content {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  min-height: 86vh;
}

#signup_content .title {
  text-align: center;
  font-weight: 600;
}

#signup_content form {
  margin-left: 0;
}

#signup_content .row {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-left: 0;
  margin: 0;
}

#signup_content form .btn {
  margin-right: 1rem;
  font-family: "Spartan", sans-serif;
}

#signup_content form .login-link {
  margin-left: 1rem;
  margin-top: 0.5rem;
}

#signup_content form .bottom-part {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

#navbar .brand-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

#navbar .brand-logo {
  padding-left: 1%;
}

#navbar .brand-logo .logo-text {
  font-weight: 800;
  font-size: 1.5rem;
  margin: 0 0 0 0.45rem;
}

#navbar .dropdown-trigger,
.btn {
  text-transform: none;
  color: var(--light);
}

.sidenav li > a > i.material-icons {
  color: var(--light);
  margin: 0 0 0 0;
}


